<template>
  <div class="header__video-wrapp">
    <div class="header__video-box">
      <video class="header__video" id='header__video' preload="metadata" autoplay>
        <source src="./../assets/city_main.mp4" type="video/mp4">
        <source src="./../assets/city_main.mp4" type="video/webm">
      </video>
    </div>
    <div class="top-menu">NALETU</div>


    <v-layout class="right" wrap>
      <img class="menu" src="./../assets/menu.svg" @click.stop="drawer = !drawer">

      <v-navigation-drawer v-model="drawer" temporary absolute right>
        <v-list class="pa-1">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="https://randomuser.me/api/portraits/men/85.jpg">
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>John Leider</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
        <v-list class="pt-0" dense>
          <v-divider></v-divider>
          <v-list-tile v-for="item in items" :key="item.title" @click="">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
    </v-layout>

    <div class="middle-text">
      <div class="nameLabel">ФОТОГРАФ НАТАЛЬЯ ЛЕТУНОВА</div>
      <div class="description">
        Путешествую с любовью
        <br> к архитектуре</div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        drawer: null,
        items: [{
            title: 'Home',
            icon: 'dashboard'
          },
          {
            title: 'About',
            icon: 'question_answer'
          }
        ]
      }
    },
    name: 'landing'
  }

</script>

<style scoped>
  .header__video-wrapp {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    margin-bottom: 100px
  }

  .header__video-box {
    text-align: center;
    margin: 0 -1000px;
    height: 100%;
  }

  .header__video {
    display: inline-block;
    vertical-align: top;
    height: 100vh;
    min-height: 768px;
    transform: scale(1.15)
  }

  .top-menu {
    /* z-index: 10; */
    position: absolute;
    top: 50px;
    left: 3%;
    color: white;
    font-size: 1.4rem;
  }

  .right {
    position: absolute;
    height: 100vh;
    width: 100%;
    top: 0;
    z-index: 10;
  }

  .right>.menu {
    position: absolute;
    top: 50px;
    right: 4%;
    width: 35px;
    cursor: pointer;
  }

  .middle-text {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    top: 0;
    /* top: 35%; */
    /* left: 40%; */
    font-size: 1.5rem;
    color: white;
  }

  .middle-text .small {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }

  .middle-text .big {
    top: 40%;
    font-size: 4rem;
    font-weight: bold;
    line-height: 65px;
  }

  .nameLabel {
    font-size: 15px;
    letter-spacing: 2px;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    text-align: center;
    z-index: 75;
  }

  .description {
    font-size: 56px;
    color: rgb(255, 255, 255);
    font-weight: bold;
    text-align: center;
    z-index: 74;
    line-height: 1.2;
  }

</style>



